/* basic styling */
body {
    margin: 0 3%;
    text-align: center;
}

a {
    text-decoration: none;
    color: black;
    padding: 10px;
}

h1 {
    text-transform: uppercase;
    font-weight: bold;
    font-size: 72px;
    color: white;
    -webkit-text-stroke: 2px darkgoldenrod;
}

h2 {
    text-transform: uppercase;
    color: white;
    font-size: small;
}

/* app styling */
.App {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: min-content min-content min-content auto;
    grid-template-areas:
        'App-title'
        'App-nav'
        'App-sorter'
        'App-main';
}

.App-title {
    grid-area: App-title;
    text-align: center;
    background-image: linear-gradient(#232c77, rgb(39, 90, 138));
    margin-top: 5px;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
}

.App-nav {
    grid-area: App-nav;
    padding: 10px;
    background-color: lightslategray;
}

.App-sorter {
    grid-area: App-sorter;
    padding: 10px;
    background-color: darkgray;
}

.App-main {
    grid-area: App-main;
    padding: 10px;
    background-image: linear-gradient(lightgray, lightblue);
    margin-bottom: 5px;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
}

.App-article-card {
    padding: 10px;
    margin: 10px 4px 16px;
    background-color: whitesmoke;
    border-radius: 4px;
}

/* App-votebar has its own grid */
.App-votebar {
    display: grid;
    grid-template-rows: min-content min-content;
    grid-template-columns: auto auto min-content;
    grid-template-areas:
        'App-votebar-title App-votebar-title App-votebar-usercard'
        'App-votebar-votepanel App-votebar-timestamp App-votebar-usercard';
    text-align: justify;
}

.App-votebar > * {
    padding: 10px;
}

.App-article-body {
    border: 1px solid lightblue;
    background-color: white;
    padding: 10px 10px 30px;
    margin: 10px 10px 30px;
    border-radius: 4px;
    text-align: left;
}

.App-votebar-title {
    grid-area: App-votebar-title;
}
.App-votebar-usercard {
    grid-area: App-votebar-usercard;
    display: grid;
    place-items: center;
    border: 1px solid lightblue;
    border-radius: 4px;
    margin: 10px;
    background-color: white;
}
.App-votebar-votepanel {
    grid-area: App-votebar-votepanel;
    place-content: center;
    text-align: center;
    display: grid;
    grid-template-rows: min-content;
    grid-template-columns: auto auto auto;
}
.App-votebar-timestamp {
    grid-area: App-votebar-timestamp;
}

.App-votebar-plusbutton, .App-votebar-minusbutton {
    place-content: center;
}

/* CommentCard styling */
.App-comment-card {
    background-color: lightgray;
    border-radius: 4px;
    margin: 10px;
    padding-bottom: 2px;
}

.App-comment-body {
    padding: 10px;
    margin: 10px;
    background-color: white;
    border: 2px solid lightblue;
    border-radius: 4px;
    text-align: left;
}

.App-all-topics {
    text-transform: capitalize;
}

/* Dropdown menu for topics is handled in CSS */
.Dropdown {
    position: relative;
    display: inline-block;
}

.Dropdown-items {
    display: none;
    position: absolute;
    z-index: 1;
    background-color: white;
    top: 28px;
    text-transform: capitalize;
}

.Dropdown:hover .Dropdown-items {
    display: grid;
}

.App-nav > a,
.Dropdown > a,
.Dropdown-items > a {
    display: inline;
}

.App-nav > a:hover,
.Dropdown > a:hover,
.Dropdown-items > a:hover {
    background-color: lightgray;
}
